.App {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #bbb;
}

.App-in {
    background-color: #0a0224;
    width: 300px;
    height: 450px;
    display: flex;
    padding: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    border-radius: 10px;
}
@media(min-width: 0px) and (max-width: 767px){
    .App{
        max-height: 812px;
    }
    .App-in {
        width: 100%;
        height: 100%;
    }
}